.thanks-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(to right, #a8e063, #56ab2f);
  font-family: 'Arial', sans-serif;
}

.thanks-card {
  background-color: white;
  border-radius: 10px;
  padding: 2rem;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  text-align: center;
  max-width: 500px;
  width: 90%;
}

.thanks-card h1 {
  color: #4CAF50;
  margin-bottom: 1rem;
}

.thanks-card p {
  color: #555;
  line-height: 1.6;
  margin-bottom: 1rem;
}

.back-button {
  display: inline-block;
  margin-top: 1rem;
  background-color: #4CAF50;
  color: white;
  text-decoration: none;
  padding: 10px 20px;
  border-radius: 5px;
  font-weight: bold;
  transition: background-color 0.3s;
}

.back-button:hover {
  background-color: #45a049;
}
